.box .head img {
    width: 100px;
    height: 100px;
    margin: 5px;
}

.box {
    display: flex;
    justify-content: left;
    margin: 20px;
    padding: 10px;
    border-bottom: 1px solid darkgrey;
}

.head {
    display: flex;
    flex-direction: column;
    margin-right: 10px;
}

.about {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin-right: 190px;
}

.activeFollowButton {
    background: whitesmoke;
}

.disabledFollowButton {
    color: lightgrey;
}

.disabledFollowButton:hover {
    background: whitesmoke;
}