.activePage {
    font-weight: bold;
    border: 2px solid black;
    padding-left: 4px;
}

.pages {
    border: 1px solid black;
    padding-left: 4px;
}

.paginator {
    margin: 10px;
    cursor: pointer;
    text-align: center;
}

.activeButton {
    padding: 6px 6px 1px 6px;
    background: whitesmoke;
}

.disabledButton {
    padding: 6px 6px 1px 6px;
    background: whitesmoke;
    color: lightgrey;
}

.disabledButton:hover {
    background: whitesmoke;
}

.preloader {
    padding: 150px 30%;
}