.header {
    grid-area: h;
    background: whitesmoke;
}

.header img {
    height: 100%;
}

.login {
    float: right;
}

.login a, .login, .login {
    color: black;
}

.login img {
    width: 45px;
    height: 45px;
    padding: 5px;
    border-radius: 50%;
    cursor: pointer;
}

.moreInfo {
    position: relative;
    top: 17px;
}

.logoText {
    position: relative;
    bottom: 35%;
    font-size: 120%;
    font-family: "Arial Unicode MS";
}

.loginButtonRoute {
    position: relative;
    top: 15px;
}

.loginButtonRoute img {
    position: relative;
    top: 5%;
    right: 3%;
    height: 25px;
    width: 25px;
    border-radius: 0;
}

.loginButtonRoute img:hover {
    position: relative;
    top: 4%;
    right: 2%;
    height: 27px;
    width: 27px;
}

.login {
    display: flex;
}

.buttonSpan {
    position: relative;
    top: 15px;
}