.nav {
    grid-area: n;
    background: lightgrey;
    padding-top: 10px;
}

.item {
    padding: 5px 25px;
}

.item:hover {
    background: lightskyblue;
}

a {
    text-decoration: none;
    color: black;
    font-size: 110%;
}

a.active {
    color: black;
}

.friends {
    text-align: center;
    padding: 40px 0 20px;
    font-size: 150%;
}

.friendName img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
}

.friendElements {
    display: flex;
    justify-content: space-around;
}