.formControl.error input,
.formControl.error textarea
{
    border: solid red 4px;
}

.formControl.error span {
    color: red;
}

.formSummaryError {
    border: solid red 1px;
    color: red;
}

.loginBlock {
    margin: 10px;
    background: whitesmoke;
    border: 1px solid darkgrey;
    width: 30%;
}

.signInText {
    background: lightgrey;
    font-size: 85%;
}

.signInForm, .signInText {
    padding: 10px;
}

.field {
    padding-bottom: 20px;
}

input {
    padding: 5px;
    width: 93%;
}

button {
    background: lightgrey;
    color: black;
    font-weight: 700;
    text-decoration: none;
    user-select: none;
    padding: .5em 2em;
    outline: none;
    border: 1px solid darkgrey;
    border-radius: 4px;
    transition: 0.2s;
}
button:hover { background: lightskyblue; }
button:active { background: whitesmoke; }

.rememberMeField {
    width: 35%;
}

.captchaText {
    padding-top: 10px;
}
